html,
body,
#root {
    height: 100%;
    overflow: hidden;
}

body {
    margin: 0;
    padding: 0;
    background-color: #e2e2e2;
    font: 14px 'Heebo', 'Lucida Grande', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
    color: #FD7162;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    color: #666666;
}